import React, {useState} from 'react';
import {
    Box,
    Button,
    FormControl,
    FormControlLabel,
    FormLabel,
    Paper,
    Radio,
    RadioGroup,
    TextField,
    Typography
} from '@mui/material';

const TrainingScoreCalculator = () => {
    const [parameters, setParameters] = useState({Vo: 1, Da: 1, Vi: 1});
    const [percentages, setPercentages] = useState({Vo: 1, Da: 1, Vi: 1});
    const [mode, setMode] = useState('pro');
    const [results, setResults] = useState({});
    const Attribute = ['Vo', 'Da', 'Vi'];
    const firstRoundScore = 165;  // 第一圈加分（选定属性）
    const secondRoundScore = 145;  // 第二圈加分（所有属性）

    const handleInputChange = (e) => {
        const {name, value} = e.target;
        setParameters((prev) => ({...prev, [name]: Number(value)}));
    };

    const handlePercentageChange = (e) => {
        const {name, value} = e.target;
        setPercentages((prev) => ({...prev, [name]: parseFloat(value)}));
    };

    const handleModeChange = (e) => {
        setMode(e.target.value);
    };

    const calculateResults = () => {
        const maxValues = mode === 'pro' ? 1500 : 1800;  // Pro 模式最大值 1500，Master 模式最大值 1800

        // 计算属性增加值
        const calculateIncrease = (score, attribute) => {
            const lessonBonus = 1 + percentages[attribute] / 100;
            return Math.floor(score * lessonBonus);
        }

        // 计算有效增加值（超过 最大值 - 30 的部分不计入）
        const calculateEffectiveIncrease = (attribute) => {
            // 选定属性
            const updatedScore = Math.min(parameters[attribute]
                + calculateIncrease(firstRoundScore, attribute)
                + calculateIncrease(secondRoundScore, attribute)
                , maxValues - 30);
            let effectiveIncrease = updatedScore - parameters[attribute];
            // 其他属性
            const otherAttributes = Attribute.filter((attr) => attr !== attribute);
            for (const attr of otherAttributes) {
                const updatedScore = Math.min(parameters[attr] + calculateIncrease(secondRoundScore, attr), maxValues - 30);
                effectiveIncrease += updatedScore - parameters[attr];
            }
            return effectiveIncrease
        };

        // 选定属性：有效增加值最大的属性
        const bestAttribute = Object.keys(percentages).reduce((a, b) =>
            calculateEffectiveIncrease(a) > calculateEffectiveIncrease(b) ? a : b
        );

        // 计算增加后的属性值
        const updatedParameters = Attribute.reduce((acc, attribute) => {
            const score = parameters[attribute];
            let increase = calculateIncrease(secondRoundScore, attribute);
            if (attribute === bestAttribute) {
                increase += calculateIncrease(firstRoundScore, attribute);
            }
            const updatedScore = Math.min(score + increase, maxValues);
            return {...acc, [attribute]: updatedScore};
        }, parameters);

        setResults({updatedParameters, mode, bestAttribute});
    };

    return (
        <Paper style={{padding: 20, maxWidth: 400, margin: 'auto', marginTop: 20}}>
            <Typography variant="h5" style={{marginBottom: 20}}>追加训练</Typography>
            <Box display="flex" gap={2} flexDirection="column">
                <Box display="flex" gap={2}>
                    <TextField
                        label="Vocal"
                        name="Vo"
                        type="number"
                        value={parameters.Vo}
                        onChange={handleInputChange}
                    />
                    <TextField
                        label="Vocal %"
                        name="Vo"
                        type="number"
                        value={percentages.Vo}
                        onChange={handlePercentageChange}
                    />
                </Box>
                <Box display="flex" gap={2}>
                    <TextField
                        label="Dance"
                        name="Da"
                        type="number"
                        value={parameters.Da}
                        onChange={handleInputChange}
                    />
                    <TextField
                        label="Dance %"
                        name="Da"
                        type="number"
                        value={percentages.Da}
                        onChange={handlePercentageChange}
                    />
                </Box>
                <Box display="flex" gap={2}>
                    <TextField
                        label="Visual"
                        name="Vi"
                        type="number"
                        value={parameters.Vi}
                        onChange={handleInputChange}
                    />
                    <TextField
                        label="Visual %"
                        name="Vi"
                        type="number"
                        value={percentages.Vi}
                        onChange={handlePercentageChange}
                    />
                </Box>
            </Box>
            <FormControl component="fieldset" style={{marginTop: 20}}>
                <FormLabel component="legend">选择模式</FormLabel>
                <RadioGroup row value={mode} onChange={handleModeChange}>
                    <FormControlLabel value="pro" control={<Radio/>} label="Pro 模式"/>
                    <FormControlLabel value="master" control={<Radio/>} label="Master 模式"/>
                </RadioGroup>
            </FormControl>
            <Box display="flex" justifyContent={"center"} style={{marginTop: 20}}>
                <Button variant="contained" onClick={calculateResults}>
                    计算
                </Button>
            </Box>

            {results.mode && (
                <Box style={{marginTop: 20}}>
                    <Typography>建议训练属性: {results.bestAttribute}</Typography>
                    <Typography>Vocal: {parameters.Vo} → {results.updatedParameters.Vo}</Typography>
                    <Typography>Dance: {parameters.Da} → {results.updatedParameters.Da}</Typography>
                    <Typography>Visual: {parameters.Vi} → {results.updatedParameters.Vi}</Typography>
                </Box>
            )}
        </Paper>
    );
};

export default TrainingScoreCalculator;
